	module.exports = {"data":{"markdownRemark":{"html":"<h2>Letter Spacing Property</h2>\n<p>The letter-spacing property adjusts the space between all letters in a block of text. It is also referred to as “tracking” in typography terms and in software with advanced type-setting options. The property accepts <code>px</code> and <code>em</code> length values, including negative values.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.first-example</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">letter-spacing</span><span class=\"token punctuation\">:</span> 3px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">.second-example</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">letter-spacing</span><span class=\"token punctuation\">:</span> -1px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">.third-example</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">letter-spacing</span><span class=\"token punctuation\">:</span> 0.5em<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><img src=\"https://github.com/kaithrendyle/guide-photos/blob/master/letter-spacing.png\" alt=\"result of CSS\"></p>\n<p>In general it is good practice to use relative units (<code>em</code>) because the spacing will always be relative to the font size you’ve declared.</p>\n<p>It is important to consider legibility when adjusting letter spacing. If letters are too close together, they can appear cramped and hard to read. On the other hand, if letters are too far apart, they might not read as a word, but instead as individual letters. This might also affect how screen readers read the text out loud to people with low vision. </p>\n<p>In general lowercase letters don’t typically need the letter spacing adjusted. You might find a need for adjustments when you use sections of uppercase if spacing looks too tight.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->","fields":{"slug":"/css/properties/letter-spacing-property/"},"frontmatter":{"title":"Letter Spacing Property"}}},"pathContext":{"slug":"/css/properties/letter-spacing-property/"}}

